import React from 'react';
import styled from 'styled-components';
import { Container } from '@components/global';
import Fade from 'react-reveal/Fade';

const Paragraph = ({ header, paragraph, img, caption }) => (
  
  <Container>
    <HeaderWrapper>
      <Grid>
        <div>
          <Fade>
            <h3>{header}</h3>
            {paragraph.map(({ text }) => {
              return (
              <Text><p>{text}</p></Text>
              );
            })} 
          </Fade>
        </div>
        <Art>
          <img src={img}/>
          <Caption>{caption}</Caption>
        </Art>
      </Grid>
    </HeaderWrapper>
  </Container>
);


const HeaderWrapper = styled.div`
  padding: 35px 0 30px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 15px 0;
    margin-left: 0%;
  }
`;


const Grid = styled.div`
  display: grid;
  grid-template-columns: 5fr 4fr;
  grid-gap: 50px;
  text-align: left;
  align-items: top;
  justify-items: center;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;
    grid-gap: 25px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const Art = styled.figure`
  margin: 30px 0;
  max-width: 1380px;
  width: 100%;
  img{
    width: 100%;
  }
`;

const Text = styled.figure`
  margin: 18px 0;
  @media (max-width: ${props => props.theme.screen.md}) {
    margin: 14px 0;
  }
`;



const Caption = styled.div`
  padding-top:5px;
  padding-right: 30%;
  ${props => props.theme.font_size.xsmall};
`;

export default Paragraph;
